import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { HTextLabelBox, VTextLabelBox } from '@cetus/ui-kit'

interface StatsInfoProps {
  label: string
  value: string | JSX.Element
  loading?: boolean
}

function StatsInfo({ label, value, loading }: StatsInfoProps) {
  const { isApp } = useWindowWidth()
  return isApp ? (
    <HTextLabelBox
      label={label}
      value={value}
      labelStyle={{ fontSize: '12px' }}
      valueStyle={{ fontSize: '14px', h: '14px', fontWeight: '500' }}
      isLoading={loading}
      skeletonStyle={{ valueW: '60px' }}
    />
  ) : (
    <VTextLabelBox
      wrapStyle={{
        gap: '8px',
        alignItems: 'flex-end'
      }}
      titleStyle={{
        fontSize: '12px'
      }}
      valueStyle={{
        fontSize: '14px',
        fontWeight: '500'
      }}
      title={label}
      value={value}
      isLoading={loading}
      skeletonStyle={{ valueW: '60px', valueH: '14px' }}
    />
  )
}

export default StatsInfo
