import { ScaleLinear } from 'd3'
import { formatNumber } from '@cetus/utils'

interface HorizontalLineProps {
  value: number
  yScale: ScaleLinear<number, number>
  width: number
  height: number
  axisLabelPaneWidth: number
  color: string
  strokeWidth: number
  strokeDasharray?: string
  showPriceLabel?: boolean
  showTriangle?: boolean
}

export const HorizontalLine = ({
  value,
  yScale,
  width,
  height,
  axisLabelPaneWidth,
  color,
  strokeWidth,
  strokeDasharray,
  showPriceLabel = true,
  showTriangle = false
}: HorizontalLineProps) => {
  const y = yScale(value)
  
  // Á°Æ‰øùÁ∫øÂßãÁªàÂú®ÂèØËßÅÂå∫ÂüüÂÜÖÔºåÂç≥‰ΩøË∂ÖÂá∫ËæπÁïå‰πüË¶ÅÊòæÁ§∫
  const clampedY = Math.max(0, Math.min(y, height))

  return (
    <g>
      <line
        x1={axisLabelPaneWidth}
        y1={clampedY}
        x2={width - axisLabelPaneWidth}
        y2={clampedY}
        stroke={color}
        strokeWidth={strokeWidth}
        strokeDasharray={strokeDasharray}
        pointerEvents="none"
      />
      
      {/* ‰ª∑Ê†ºÊ†áÁ≠æ - Ê†πÊçÆ showPriceLabel Â±ûÊÄßÊéßÂà∂ÊòæÁ§∫ */}
      {showPriceLabel && (
        <text
          x={width - axisLabelPaneWidth + 10}
          y={clampedY - 5}
          fontSize="12px"
          fill={color}
          textAnchor="start"
        >
          {formatNumber(value, 4)}
        </text>
      )}

      {/* ÁôΩËâ≤‰∏âËßíÂΩ¢ÂõæÊ†á - ‰Ωç‰∫éÁ∫øÁöÑÂ∑¶‰æß */}
      {/* {showTriangle && <path
        d={`M ${axisLabelPaneWidth - 2} ${clampedY - 5} L ${axisLabelPaneWidth + 6} ${clampedY} L ${axisLabelPaneWidth - 2} ${clampedY + 5} Z`}
        fill="white"
        stroke="none"
        pointerEvents="none"
      />} */}

      {showTriangle && <circle cx={axisLabelPaneWidth+4} cy={clampedY} fill="#fff" r="4" />}

        
    </g>
  )
}
